#eff_bl {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  background-color: #000;
  opacity: 0.8;
  z-index: 1002;
  overflow-y: hidden; }

.mob_menu {
  display: none;
  padding: 20px 0;
  background: #222;
  position: fixed;
  width: 100%;
  z-index: 1003;
  border-bottom: 1px solid  #2d2d2d;
  text-align: center;
  box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.35); }
  @media only screen and (max-width: 991px) {
    .mob_menu {
      display: block; } }
  .mob_menu .mob_logo {
    display: inline-block;
    max-width: 125px;
    margin: 0 auto; }
    .mob_menu .mob_logo img {
      width: 100%;
      height: auto; }
  .mob_menu .searchBtn {
    width: 42px;
    color: #fff;
    font-size: 26px;
    position: absolute;
    top: 50%;
    left: 20px;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    cursor: pointer; }
  .mob_menu .search_nav {
    position: absolute;
    top: 100%;
    width: 100%;
    display: none;
    margin-top: 1px;
    padding: 30px 20px;
    background-color: #000;
    box-shadow: 0 6px 40px 0 rgba(0, 0, 0, 0.76); }
    .mob_menu .search_nav .search_form input {
      width: 100%;
      font-weight: 300;
      background-color: #fff;
      border: none;
      border-radius: 15px;
      padding: 4px 10px 3px 15px;
      color: #000; }
  .mob_menu .menu-icon-wrp {
    display: block;
    position: absolute;
    top: 12px;
    right: 30px;
    width: 40px;
    height: 40px;
    margin: 0 auto;
    padding-top: 12px;
    transition: all 500ms cubic-bezier(1, 0, 0.645, 0.65);
    z-index: 1003; }
    @media only screen and (max-width: 425px) {
      .mob_menu .menu-icon-wrp {
        right: 21px; } }
    @media only screen and (max-width: 320px) {
      .mob_menu .menu-icon-wrp {
        right: 18px; } }
    .mob_menu .menu-icon-wrp .menu-bar {
      display: block;
      position: relative;
      background: #fff;
      width: 32px;
      height: 3px;
      border-radius: 1px;
      transition: all 300ms;
      margin: 0 auto; }
    .mob_menu .menu-icon-wrp .bar1 {
      top: 0;
      animation: topbar-back 500ms;
      animation-fill-mode: both;
      animation-play-state: initial !important; }
    .mob_menu .menu-icon-wrp .bar2 {
      opacity: 1; }
    .mob_menu .menu-icon-wrp .bar3 {
      bottom: 0;
      animation: bottombar-back 500ms;
      animation-fill-mode: both;
      animation-play-state: initial !important; }
  .mob_menu .menu-bar + .menu-bar {
    margin-top: 5px; }
  .mob_menu .toggled .bar1 {
    top: 25%;
    animation: topbar-x 500ms;
    animation-fill-mode: both; }
  .mob_menu .toggled .bar2 {
    opacity: 0; }
  .mob_menu .toggled .bar3 {
    bottom: 31%;
    animation: bottombar-x 500ms;
    animation-fill-mode: both; }

@keyframes topbar-x {
  0% {
    top: 0;
    -webkit-transform: rotate(0deg); }
  45% {
    top: 25%;
    -webkit-transform: rotate(145deg); }
  75% {
    -webkit-transform: rotate(130deg); }
  100% {
    -webkit-transform: rotate(135deg); } }

@keyframes topbar-back {
  0% {
    top: 25%;
    -webkit-transform: rotate(135deg); }
  45% {
    -webkit-transform: rotate(-10deg); }
  75% {
    -webkit-transform: rotate(5deg); }
  100% {
    top: 0;
    -webkit-transform: rotate(0deg); } }

@keyframes bottombar-x {
  0% {
    bottom: 0;
    -webkit-transform: rotate(0deg); }
  45% {
    bottom: 25%;
    -webkit-transform: rotate(-145deg); }
  75% {
    -webkit-transform: rotate(-130deg); }
  100% {
    -webkit-transform: rotate(-135deg); } }

@keyframes bottombar-back {
  0% {
    bottom: 25%;
    -webkit-transform: rotate(-135deg); }
  45% {
    -webkit-transform: rotate(10deg); }
  75% {
    -webkit-transform: rotate(-5deg); }
